@font-face {
  font-family: 'Source Code Pro', monospace;
  src: url('https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@500&display=swap');
}
body {
  background-color: rgb(229, 240, 239);
}

.App {
  text-align: center;
  font-family: 'Source Code Pro', monospace;
}

ul {
  list-style-type: none;
  border: 5px solid rgb(106, 106, 232);
  border-radius: 10px;
  background-color: rgb(35, 147, 245);
  color: white;
  display: flex;
  align-content: center;
  flex-direction: column;
  line-height: 45px;
  margin: 20px;
  z-index: 1; 
}

form {
  margin: 20px;
  background-color: rgb(237, 112, 112);
  padding: 10px;
  border: 5px solid rgb(250, 66, 66);
  border-radius: 10px;
  z-index: 1;
}

footer {
  position: fixed;
  background-position: 0% 0%;
  z-index: 2;
}

.notActive {
  display: none;
}

.forms {
  display: flex;
  flex-direction: column;
  line-height: 1.5;
  justify-content: center;
  text-align: center;
  align-items: center;
}

.button {
  width: 100px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  background-color: rgb(148, 12, 12);
  color: white;
}

.get-FEC {
  text-decoration: none;
  border: 2px solid rgb(250, 66, 66);
  background-color: rgb(237, 112, 112);
  border-radius: 12px;
  padding-left: 5px;
  padding-right: 5px;
  color: white
}

a {
  text-decoration: none;
  color:rgb(6, 6, 6)
}

.text-input {
  border-radius: 8px;
}